@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
:root {
  --primary-color: #0410f8;
  --primary-dark: #6e00fd;
  --secondary-color: #0011ff;
  --secondary-dark:#e3d002;
  --overlay: rgba(26, 21, 0, 0.45);
  --background: rgb(255, 255, 255);
  --text: #000000;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.2rem;
  line-height: 1.2;
  width: 100%;
  overflow-x: hidden;
  align-items: center;
  justify-content: center;
  display: flex;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;

}

a:link {
  text-decoration: inherit;
  color: inherit;
  cursor: pointer;
}

a:visited {
  text-decoration: inherit;
  color: inherit;
  cursor: auto;
}

a:hover {
  text-decoration:3px solid underline;
  text-decoration-color:  var(--primary-color);

}

.col {
      width: 50%;
      padding: 0;
      float: left;
  }

/*Main container*/

.container {
  background-color: var(--background);
  height:100vh;
  width:100%;
  position: relative;
  padding: 90px;
  margin: auto;
  color: var(--text);
}

.title {
  border-bottom: 3px solid var(--secondary-color);
  font-size: 3rem;
  font-weight: 500;
  width: fit-content;
}

.button {
  padding: 17px 28px;
  font-size: 1.2rem;
  color: rgb(0, 0, 0);
  font-weight: 600;
  text-transform: uppercase;
  border: 1px solid var(--primary-color);
  border-radius: 4px;
  cursor: pointer;
  background: linear-gradient(to right, var(--primary-color) 50%, white 50%);
  background-size: 200% 100%;
  background-position: right bottom;
  transition: all .5s ease-out;
}

.button:hover {
  border: 1px solid var(--primary-dark);
  color:white;
  background-position: left bottom;
}

.line {
  display: block;
  width: 50%;
  height: 3px;
  margin: 5px auto;
  background-color: var(--primary-color);
}

.list-table {

  margin-top: 2rem;
}

.date {
  font-size: 1rem;
  font-weight: 800;
  color: white;
  border-radius: 1em;
  width: fit-content;
  padding: 0.5rem;
  margin-top: 1rem;
  background: var(--primary-color);
}

.list-primary {
  font-size: 1.7rem;
  font-weight: 500;
  margin-top: 0.5rem;
}

.list-secondary{
  font-size: 1.5rem;
  font-weight: 500;
  margin-top: 0.5rem;
}
.list-tertiary{
  font-size: 1.3rem;
  font-weight: 300;
  margin-top: 0.5rem;
}

@media screen and (max-width:940px) {

  .container {
      display: block;
      width: 100%;
      grid-template-columns: 1fr;
      height: 100vh;
      padding: 90px 10px 10px;
  }
  .col {
      width: 100%;
      padding: 0;
      text-align: center;
  }
  .title {
    font-size: 2rem;
  }

  .list-table {
    background: var(--overlay);
    margin-top: 0.5rem;
  }
  
  .date {
    font-size: 1rem;
    font-weight: 500;
    color: var(--text);
    border-radius: 1em;
    width: fit-content;
    padding: 0.3rem;
    margin-top: 0.7rem;
    background: var(--primary-color);
  }
  
  .list-primary {
    font-size: 1.7rem;
    font-weight: 500;
    margin-top: 0.5rem;
  }
  
  .list-secondary{
    font-size: 1.5rem;
    font-weight: 500;
    margin-top: 0.5rem;
  }
  .list-tertiary{
    font-size: 1.3rem;
    font-weight: 300;
    margin-top: 0.5rem;
  }

  
}