.side-nav {
    position: fixed;
    height: 14rem;
    width: 2rem;
    top:40%;
    right: 0;
    z-index: 1;
    transition: .3s ease-in;
    overflow: hidden;
}

.side-nav .side-item {
    display: block;
    width: 14px;
    height: 14px;
    margin: 7px;
    position: relative;
    z-index: 1;
}

.dot {
    border-radius: 50%;
    position: absolute;
    height: 4px;
    width: 4px;
    border: 0;
    background: rgb(255, 255, 255);
    left: 50%;
    top: 50%;
    transition: 0.5s ease-in-out;
}

.active-dot {
    height: 12px;
    width: 12px;
    margin: -6px 0 0 -4px;
    border-radius: 100%;
    transition: all .1s ease-in-out;
    position: absolute;
    border: 0;
    background: rgb(255, 255, 255);
    left: 50%;
    top: 50%;
    transition: 0.5s ease-in-out;
}